<template>
    <section id="experience">
        <div class="text row">
          <h1 v-if="!eng" class="col-md-3 text-left">DENEYIM</h1>
          <h1 v-if="eng" class="col-md-3 text-left">WORK</h1>
          <div class="col-md-9 center">
            <h2 v-if="!eng">İstanbul Teknik Üniversitesi</h2>
            <h2 v-if="eng">Enuygun.com</h2>
            <ul>
              <li v-if="!eng">Bilgisayar Mühendisliği</li>
              <li v-if="eng">Part Time</li>
              <li>/ July 2018 - Cont.</li>
            </ul>
          </div>
        </div>
    </section>
</template>

<script>
export default {
    data: () => {
        return {
            eng: true,
        }
    }
}
</script>

<style lang="scss">
#experience{
    padding: 70px 0% 3% 0;  
    
    font-size: 18px;
    line-height: 30px;

    .text{
      color: white;


      h1{
        opacity: 0.95;
      }

      h2{
        color: rgb(240, 240, 240);
      }

      ul{
        color: rgb(143, 143, 143);
        li{
          display: inline;
          // overflow: ;
          list-style-type: disc;
          // display: list-item;
        }
      }

      li{
        color: rgb(143, 143, 143);
        list-style-type: none;
      }
    }
  }
</style>





